import axios from "axios";

const fetchTodos = () => {
  return axios
    .get("http://localhost:2222/todos")
    .then((response) => response.data);
};

const addTodo = (todo) => {
  return axios
    .post("http://localhost:2222/todos", todo)
    .then(({ data }) => data);
};

const deleteTodo = (todoId) => {
  return axios.delete(`http://localhost:2222/todos/${todoId}`);
};

const updateTodo = (todoId, update) => {
  return axios
    .patch(`http://localhost:2222/todos/${todoId}`, update)
    .then(({ data }) => data);
};

const newData = { fetchTodos, addTodo, deleteTodo, updateTodo };
export default newData;
